import React, { Component } from 'react';
import styled from 'styled-components';
import { MODAL_TYPES } from '../../constants';

const Container = styled.div`
  width: 100%;
  height: 30px;
  text-align: center;
  padding: 18px 0;
  background: ${props => props.theme.base.background};
`;

const NavContents = styled.div`
  margin: 0 auto;
  width: 100%;
  height: 100%;
  display: flex;
  padding: 3px 0;
  vertical-align: middle;
`;

const LogoContainer = styled.div`
  margin-left: 36px;
  float: left;
`;

const SettingContainer = styled.div`
  margin-right: 36px;
  float: right;
`;

/*
 * When push we add new content to the modalContent
 * Modal detects change and will activate, so actually modal lives on the base app.
 *
 */

class TopNav extends Component {
  constructor(props) {
    super(props);
  }

  // TODO: make global settings shown state
  showSettings = (extraProps) => {
    this.props.addModal(MODAL_TYPES.SETTINGS, {});
  }

  render() {
    return <Container>
      <NavContents>
        <LogoContainer>LOGO</LogoContainer>
        <SettingContainer>
          <button type="button" onClick={this.showSettings}>⚙️</button>
        </SettingContainer>
      </NavContents>
    </Container>;
  }
}

export default TopNav;
